.wrapper {
  width: 20%;
  height: 100%;
  padding-left: 12%;
  position: absolute;
  overflow-y: auto;
  overflow-x: auto;
  background-color: #ffFCF4;

}

.textbox {
  padding-top: 20%;
}

.textbox h1{
  font-family: Lato;
  font-style: normal;
  font-weight: 900;
  font-size: 15px;
  line-height: 42px;
  letter-spacing: 0.05em;


  color: #000000;
  padding-left: 0%;
  margin-bottom: 0%;
  margin-top: 10%;
}

.textbox p{
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 0.1em;
  margin-top: 1%;
  color: #000000;
}

.textbox a{
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 42px;
  letter-spacing: -0.02em;
  text-decoration: none;
  color: #1EA5AD;

}

.textbox a:hover{
  color: #E76060;
}

.pryde{
  background: repeating-linear-gradient(60deg, #1796E0, #99D85D, #FFAB3F);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pryde:hover{
  background: repeating-linear-gradient(60deg, #1796E0, #99D85D, #FFAB3F, #1796E0);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 800% 800%;
  animation: move 6s ease infinite;
}

