.cover {
  margin-left: 37%;
  margin-right: 5%;
  padding-top: 4%;
  width: 58%;
  position: fixed;
  z-index: 999;
  height: 100%;
  background-color: #ffFCF4;
  vertical-align: middle;
  display: none;
}

.cover img{
  width: 100%;
  background-repeat: repeat;
  opacity: 1;
  transition: 0.5s;  
}

@keyframes move {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 25%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@media only screen and (max-width: 800px){
  .textbox a{
    font-size: 25px;
    line-height: 35px;
    letter-spacing: -0.02em;
  }  

  .textbox h1{
    font-size: 12px;
    line-height: 35px;
    letter-spacing: 0.05em;
  }
  
  .textbox p{
    font-size: 10px;
    letter-spacing: 0.1em;
    margin-top: 1%;
  }
}