.wrapper {
  width: 12%;
  height: 100%;
  background-color: #ffFCF4;
}

.logo img{
  display: flex;
  height: 7%;
  width: auto;
  padding-left: 5%;
  margin-top: 5%;
  position: fixed;
  z-index: 999;
}

.mail img{
  display: flex;
  height: 4%;
  width: auto;
  padding-left: 5.5%;
  margin-top: 11%;
  position: fixed;
  z-index: 999;
}

.linked img{
  display: flex;
  height: 5%;
  width: auto;
  padding-left: 5.5%;
  margin-top: 16%;
  position: fixed;
  z-index: 999;
}

.insta img{
  display: flex;
  height: 5%;
  width: auto;
  padding-left: 5.5%;
  margin-top: 21.5%;
  position: fixed;
  z-index: 999;
}

.resume img{
  display: flex;
  height: 5%;
  width: auto;
  padding-left: 5.5%;
  margin-top: 27%;
  position: fixed;
  z-index: 999;
}

.github img{
  display: flex;
  height: 5%;
  width: auto;
  padding-left: 5.5%;
  margin-top: 32%;
  position: fixed;
  z-index: 999;
}

.name {
  margin-top: 5%;
  height: 85%;
  width: auto;
  position: fixed;
  z-index: 1;
}

@media only screen and (max-width: 800px){
  .name{
    display: none;
  }

  .logo img{
    padding-left: 3%;
    padding-top: 5%;
  }
  
  .mail img{
    padding-left: 3.75%;
    padding-top: 13%;
  }
  
  .linked img{
    padding-left: 3.75%;
    padding-top: 18%;
  }
  
  .insta img{
    padding-left: 3.75%;
    padding-top: 24%;
  }
  
  .resume img{
    padding-left: 3.75%;
    padding-top: 30.5%;
  }
  
  .github img{
    padding-left: 3.75%;
    padding-top: 37%;
  }
}